import React, { PureComponent } from "react";
import Card from "./Elements/Card";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import '../scss/ThemesSplide.scss'
import "./scss/SlideShow.scss"
import { getColumns, getSizeInPercent } from "../../../utils";

class SlideShow extends PureComponent {
  constructor(props) {
    super(props)
    this.myRef = React.createRef();
  }
  state = {
    currentIndex: 0
  }
  // componentDidMount() {
  //   const { heightUpdate } = this.props
  //   setTimeout(() => heightUpdate(), 1000)
  // }
  handleSlideChange = () => {
    const { currentIndex } = this.state
    const nextSlideIndex = currentIndex + 1;
    if (this.myRef && this.myRef.current) {
      if (nextSlideIndex < this.myRef.current.slides.length) this.myRef.current.splide.go(nextSlideIndex)
      else this.myRef.current.splide.go(0)
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) return true
    else if (this.state.currentIndex != prevState.currentIndex) return true;
    else return false
  }


  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ThemeInfo, clickToShowPopUp, onClickToCTA, onClickPopUpSlider, containerSize, ProductSetting} = this.props;
    const responsiveColumns = Math.max((containerSize.width / (CardStyle.minimumWidth + 20)).toFixed(), 1)
    const columns =  getColumns(containerSize.width, ThemeStyle.column, responsiveColumns);
    const postSize = getSizeInPercent(containerSize.width, columns)
    const delaySetting = ThemeStyle?.slide?.slideDelay || 5;
    const settings = {
      type: 'fade',
      rewind: true,
      speed: 1000,
      perPage: 1,
      perMove: 1,
      gap: 0,
      autoplay: false,
      pauseOnHover: false,
      padding: 0,
      pagination: false,
      arrows: false,
    };
    const { currentIndex } = this.state;
    return (
      <div className="tb_ss_post_container">
        <Splide hasTrack={false} className="tb_ss_slider_wrappper" options={settings} ref={this.myRef} onActive={(splide, prev, next) => this.setState({ currentIndex: prev.index })}>
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} >
                  <Card key={index} postSize={postSize} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} />
                </SplideSlide>
              })}
          </SplideTrack>
        </Splide>
      </div>
    );
  }
}

export default SlideShow
