import React, { PureComponent } from "react";
import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Seprator from "../../Elements/Seprator/Seprator";
import { isInstaUser } from "../../../../utils";
export default class AuthorDetails extends PureComponent {
  render() {
    // const { author, personalization, postTime, network} = this.props;
    const {Post, CardStyle, ThemeInfo, inView} = this.props;
    const { author, networkId } = Post
    const { ThemeId } = ThemeInfo.id
    const instaUser =  isInstaUser(author.name);
    const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
    const postAuthor = (CardStyle.author.status && !instaUser) ? true : false;
    const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !instaUser && username != '') ? true : false;

    // const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
    // const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
    // const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;

    return (
      <div className="tb_ss_author_wrapper">
        <div className="tb_ss_author">
          {postAuthor ? 
          // <Author author={author} authorClass={'tb_ss_author_profile'} network={network} /> 
          <Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_ss_author_profile'} inView={inView} /> 
          : ''}
          <div className="tb_ss_author_info">
            {postAuthor ? <div className="tb_ss_authorname">{author.name}</div> : ''}
            <div className="tb_ss_post_info">
              {postAuthor ? <div className="tb_ss_username">{username}</div> : ''}
              {authorSeprator ? <Seprator sepClass={`tb_ss_seprator`} /> : ''}
              {CardStyle.timeStatus ? 
              // <Time postTime={postTime} timeClass={'tb_ss_time'} /> 
              <Time createdAt={Post.createdAt} timeClass={`tb_ss_time${CardStyle.author.variant}`} />
              : ''}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
