import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import Rating from "../../Elements/Rating/Rating";
import { PostTrimContent } from '../../../../utils'
import Network from "../../Elements/Network/Network";
import { isEmbed, isSocialEmbed } from "../../../../actions/api";
const trim_content_length = 50;
export default class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      contentHeight: 0,
    };
    this.contentRef = React.createRef();
  }

  // componentDidMount() {
  //   // Set initial height
  //   this.updateHeight();
  //   const { currentIndex, intervalTime, itemIndex, itemData, personalization } = this.props
  //   if (currentIndex == itemIndex && (itemData?.type == 2 || itemData?.type == 4 || ((itemData?.type == 3 || itemData?.type == 5) || (itemData?.type == 1) && personalization?.playFullVideo == 0)) || (itemData?.type == 1 && personalization?.playFullVideo == 1)) {
  //     this.intervalId = setTimeout(this.updateIndex, intervalTime * 1000);
  //   }

  //   // Add resize event listener
  //   window.addEventListener('resize', this.updateHeight);
  // }
  // updateIndex = () => {
  //   const { handleSlideChange } = this.props
  //   handleSlideChange()
  // }

  componentWillUnmount() {
    // Remove resize event listener
    window.removeEventListener('resize', this.updateHeight);
    clearInterval(this.intervalId);
  }

  updateHeight = () => {
    if (this.contentRef.current) {
      this.setState({ contentHeight: this.contentRef.current.offsetHeight });
    }
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { itemIndex, personalization } = this.props
    if (nextProps.currentIndex != itemIndex) {
      clearInterval(this.intervalId)
    }
    // if (nextProps.currentIndex == itemIndex && !isSocialEmbed && ([2,4].includes(nextProps.Post.type) || (([3,5].includes(nextProps.Post.type) || nextProps.Post.type == 1) && personalization.playFullVideo == 0)) || (nextProps.itemData.type == 1 && personalization.playFullVideo == 1)) {
    //   this.intervalId = setTimeout(this.updateIndex, nextProps.intervalTime * 1000);
    // }
  }


  render() {
    const { contentHeight } = this.state
    const { postSize, clickToShowPopUp, index, onClickToCTA, ThemeStyle, CardStyle, Post, ThemeInfo, counter } = this.props;
    const themeId = ThemeInfo?.id;
    const mediaType = Post?.type !== 1;
    const is_imagePost = (Post?.type === 2 || Post?.type === 4) && themeId == 77 ? true : false;
    const rating = Post?.rating > 0;
    const contentClass = (Post?.rating > 0) ? 'tb_ss_rating_content' : 'tb_ss_content'
    const textDecorate = (Post?.type === 1 && CardStyle?.style?.textDecoration) ? 'tb_ss_text_decoration tb_ss_text_post' : '';
    const postTextContent = CardStyle?.content?.trimcontent ? PostTrimContent(Post?.content?.text, trim_content_length) : Post?.content?.text;

    const socialIcon = CardStyle?.actionStatus ? true : false
    const onlyMedia = (Post?.type != 1 && CardStyle?.content?.status && !socialIcon) ? true : false

    console.log("postTextContent=====", onlyMedia)

    const imageStyle = {
      backgroundImage: `url(${Post.postFileNew})`
    };

    const themeClass = themeId == 74 ? ' tb_mss_t' : themeId == 76 ? ' tb_css_t' : themeId == 77 ? ' tb_zss_t' : ''

    const lineClamp = ((contentHeight - 350) / 30).toFixed()


    return (
      <div id={`tb-ss-post-${Post?.id}`} className={`tb_ss_post_wrapper${themeClass}`} tb-network={Post.network.icon}>
        {themeId == 74 ?
          <div className="tb_ss_blur_bg" style={imageStyle}><div></div></div>
          :
          themeId == 76 ?
            <div className="tb_ss_bg_color" data-network={Post.network.id}><div></div></div>
            : null
        }
        <div className={`tb_ss_post_article${themeId == 77 ? ' tb_ss_post_article_full' : ''}${Post.type === 1 ? ' tb_ss_text_article' : ''}`}>
          <div className="tb_ss_post_in">
            {mediaType ? <div className={`tb_ss_post_media_wrapp ${is_imagePost ? ` tb_ss_post_animated_media` : ``}`}>
              {/* <Media itemData={{ ...Post, postFileNew: itemData.filename, loopStop: personalization.playFullVideo == 1 ? true : false }} wallID={wallID} ownerId={ownerId} ThemeRule={ThemeRule} wall={wall} autoPlay={!isEmbed} handleVideoEnded={personalization.playFullVideo == 1 ? this.updateIndex : {}} intervalTime={intervalTime} /> */}
              <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} inView={true} />
            </div> : ''}
            {/* {!onlyMedia ? */}
              <div className="tb_ss_contant_wrapper" ref={this.contentRef}>
                <div className="tb_ss_contant_wrap_in">
                  {socialIcon ?
                    <div className="tb_ss_social_">
                      {/* <Network networkClass={'tb_ss_social_ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /> */}
                      <Network networkClass={'tb_ss_social_ico'} networkId={Post.network.id} network={Post.network} iconStyle={CardStyle.icon} />
                    </div> : ''}

                  {rating ? <div className="tb_ss_rating_ _">
                    {/* <Rating rating={itemData.rating} network={itemData.network} /> */}
                    <Rating rating={Post.rating} networkId={Post.networkId} />
                  </div>
                    : ''}
                  {CardStyle.content.status ?
                    // <PostContent contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={itemData.font} maxLines={lineClamp} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> 
                    <PostContent contentClass={`${contentClass} ${textDecorate}`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.content.title} />
                    : ''}
                  {/* <AuthorDetails postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} /> */}
                  <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} mediaType={mediaType} ThemeInfo={ThemeInfo} inView={true} />
                </div>
              </div>
              {/* : ''} */}
          </div>
        </div>
      </div>
    );
  }
}
