import { isEmbed, isTagbox, isTagShop } from "../../../../actions/api";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ Post, ThemeStyle, CardStyle, ThemeInfo, index, inView }) => {
  const autoPlay = (([3, 5].includes(Post.type)) && ThemeStyle.autoPlay == 1) || false
  const size = isTagbox && CardStyle.style.ratio ? CardStyle.style.ratio : false
  const isVideo = [3, 5].includes(Post.type) && autoPlay
  return <div className="tb_ss_media_wrap">
    {isVideo ?
      // <Video VideoClass="tb_ss_video" data={itemData} wallID={wallID} themeID={wall?.ThemeInfo.id} ownerId={ownerId} size={1} isCover={true} controls={false} autoPlay={!isEmbed} muted={true} handleVideoEnded={handleVideoEnded} intervalTime={intervalTime} />
      <Video VideoClass="tb_ss_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={autoPlay} muted={true} index={index} inView={true} />
      :
      // <Image ImageClass="tb_ss_image" data={itemData} wallID={wallID} themeID={wall?.ThemeInfo.id} ownerId={ownerId} size={1} ThemeRule={wall.ThemeRule} />
      <Image ImageClass="tb_ss_image" Post={Post} size={size} blurBG={false} observer={true} index={index} inView={true} />
   }
  </div>
}
export default Media